import Vue from "vue";
import Router from "vue-router";
import SwapHeader from "./layout/SwapHeader";
import SwapFooter from "./layout/SwapFooter";
import AppHeader from "./layout/AppHeader";
import AppFooter from "./layout/AppFooter";
import Home from "./views/Home.vue";
import Litepaper from "./views/Litepaper.vue";
import Landing from "./views/Landing.vue";
import Login from "./views/Login.vue";
import Register from "./views/Register.vue";
import Profile from "./views/Profile.vue";

import init from './init'
import {contract as currentContract, changeContract} from './contract'
import * as common from './utils/common.js'

const PoolApp = () => import('./components/PoolApp.vue')
const Swap = () => import('./components/swap/Swap.vue')
const Deposit = () => import('./components/deposit/Deposit.vue')
const Withdraw = () => import('./components/withdraw/Withdraw.vue')

Vue.use(Router);

const router = new Router({
  mode: 'history',
  base: process.env.BASE_URL,
  linkExactActiveClass: "active",
  routes: [
    {
      path: "/",
      name: "homepage",
      components: {
        header: AppHeader,
        default: Home,
        footer: AppFooter
      },
      meta: {
        title: "Convergence Protocol | designed to connect the worlds of utility and security token. We decentralize access to real world assets."
      }
    },
    {
      path: "/litepaper",
      name: "litepaper",
      components: {
        header: AppHeader,
        default: Litepaper,
        footer: AppFooter
      },
      meta: {
        title: "Convergence Protocol | designed to connect the worlds of utility and security token. We decentralize access to real world assets."
      }
    },
    {
      path: '/:pool(compound|usdt|y|iearn|busd|susdv2|pax|tbtc|ren|sbtc)/',
      name: 'Index',
      components: {
        header: SwapHeader,
        default: PoolApp,
        footer: SwapFooter,
      },
      children: [
        {
          path: '/swap',
          name: 'Swap',
          component: Swap,
        },
        {
          path: '/deposit',
          name: 'Deposit',
          component: Deposit,
        },
        {
          path: '/withdraw',
          name: 'Withdraw',
          component: Withdraw,
        },
        // {
        //   path: 'withdraw_old',
        //   name: 'WithdrawOld',
        //   beforeEnter: (to, from, next) => {
        //     if(to.params.pool == 'susd') return next()
        //     return next('/' + to.params.pool + '/withdraw')
        //   }
        // },
        // {
        //   path: 'stats',
        //   name: 'Stats',
        //   component: Stats
        // },
        // {
        //   path: 'faq',
        //   name: 'FAQ',
        //   component: FAQ
        // },
        // {
        //   path: 'donate',
        //   name: 'Donate',
        //   component: Donate
        // },
        // {
        //   path: 'profit/:address?',
        //   name: 'Profit',
        //   component: ProfitRouter
        // },
        // {
        //   path: 'audits',
        //   name: 'Audits',
        //   component: Audits,
        // },
        // {
        //   path: 'risks',
        //   name: 'PoolRisks',
        //   component: PoolRisks,
        // },
      ]
    },
    {
      path: "/landing",
      name: "landing",
      meta: {
        title: "Decentral Bank | A all-in-one decentralised protocol bank. Trust in the system."
      },
      components: {
        header: AppHeader,
        default: Landing,
        footer: AppFooter
      }
    },
    {
      path: "/login",
      name: "login",
      components: {
        header: AppHeader,
        default: Login,
        footer: AppFooter
      }
    },
    {
      path: "/register",
      name: "register",
      components: {
        header: AppHeader,
        default: Register,
        footer: AppFooter
      }
    },
    {
      path: "/profile",
      name: "profile",
      components: {
        header: AppHeader,
        default: Profile,
        footer: AppFooter
      }
    }
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});

const pools = ['compound','usdt','y','iearn','busd','susd', 'susdv2','pax','tbtc','ren', 'sbtc']

router.beforeEach(async (to, from, next) => {
  if(from.path.includes('/compound/withdraw_old')) await common.update_fee_info()
  //if(from.path.includes('profit') && to.path.includes('profit')) return window.location.href = to.path
  if(['Donate', 'StatsDaily', 'Audits'].includes(to.name)) return next();
  if(to.name == 'RootIndex') {
    // init('y');
    return next();
  }
  let subdomain;
  if(pools.includes(to.path.split('/')[1])) subdomain = to.path.split('/')[1]
  else subdomain = window.location.hostname.split('.')[0]
/*  if(window.location.hostname.split('.').length > 1) subdomain = window.location.hostname.split('.')[0]
  else subdomain = to.path.split('/')[1]*/
  if(subdomain == 'y') subdomain = 'iearn'
  if(!pools.includes(subdomain)) subdomain = 'iearn'

  if(!['ren', 'sbtc'].includes(subdomain)) {
    currentContract.swapbtc = false
  }

  if((currentContract.currentContract != subdomain && !['Stats', 'FAQ', 'Donate'].includes(to.name)) || ['Stats', 'FAQ', 'Donate'].includes(from.name)) {
    changeContract(subdomain)
    currentContract.currentContract = subdomain
    next();
  }
  else if(!['Stats', 'FAQ', 'Donate'].includes(to.name)) {
    next();
    if(!currentContract.initializedContracts) {
      // await init(subdomain);
    }
  }
  else {
    currentContract.currentContract = subdomain;
    return next();
  }
})

router.afterEach(async (to, from, next) => {

})

export default router
